import React, { useEffect, useState } from 'react'
import './CSSS/Api.css'
import Apibox from './Apibox'
import axios from 'axios'

function Api() {
  const [apis, setapis] = useState({ api:[] })
  useEffect(() => {
    axios.get('http://localhost:6001/apis/getapi', {
      withCredentials: true
  }).then(async (res) => {
     
          setapis({ api: res.data.apis})

   
     
  }).catch((e) => {
      console.log(e)

  })


  },[])
  return (
    <div className='api-main'>
      {
        apis.api.map((e) => {
          return <div className="api-child" key={e._id}>
            <Apibox name={e.name} description={e.description} url={e.url} />

          </div>
        })
      }

    </div>
  )
}

export default Api
