import React, { Component } from 'react'
// import icon from './css/user.png';
import './CSSS/Blog.css';
// import img from './css/download (4).jpeg';

export class Blog extends Component {

      render() {
            const { note } = this.props;
            return (
                  <div>

                        <div className="card">
                              <div className="caption">
                                    {note.title.slice(0, 30)}
                              </div>

                              <div className="account"   >

                                    <div className="info">
                                          <div className="tag">
                                                <span>{note.tag}</span>
                                                
                                          </div>
                                          <div className="time">
                                                {note.date}
                                          </div>
                                    </div>


                              </div>


                              <div className="body">
                                    {note.body}
                                   
                              </div>



                        </div>





                  </div>
            )
      }
}

export default Blog
