import React, { Component } from 'react'
import Blog from './Blog'

import './CSSS/Blogitem.css'

export class Blogitem extends Component {

          constructor(){
                    super()
                    this.state={
                              post:[],

                                        
                    

                    }
          }
          async componentDidMount (){
                    let url  = "http://localhost:6001/blog/fetchblog";
                    let a = await fetch(url);
                    let stringify = await a.json();
                    this.setState({
                              post:stringify.Blogs,
                    })

          }
  render() {
    return (
      <div className='blogmain'>
            <div className="blogs">
                    <div className="blogprofiles">


                    </div>
                    <div className="blog">
                    {
                              this.state.post.map((e)=>{
                                        return <div className="blogpost" key={e._id}><Blog note={e}  />

                                        </div> 
                              })
                    }

                    </div>
                    

            </div>
        
      </div>
    )
  }
}

export default Blogitem
