import React from 'react'
import './CSSS/project.css'

function Project({props}) {
  return (
    <div className='project-scroll-main'>
     
          <div className="img">
          <p className="title">
        {
          props.title
        }
      </p>
            <img src={props.image} alt="loading" />
          </div>
          <span className="links">
            {props.description}
          </span>
          <span className="links">
            {props.link}
          </span>
      
    </div>
  )
}

export default Project
